/* placesPageCards */

.blog-post__container{
  width: 100vw;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: #eee; */
  padding: 1rem 1.5rem;
}

.blog-post{
  width:100%;
  height: 40;
  max-width: 98rem;
  padding: 3rem;
  background-color: rgb(0, 0, 0);
  box-shadow: 0 1.4rem 3rem rgba(0,0,0,1);
  display: flex;
  align-items: center;
  border-radius: 0.8rem;
}

.blog-post__img{
  min-width: 35rem;
  max-width: 35rem;
  height: 20rem;
  -webkit-transform: translateX(-8rem);
          transform: translateX(-8rem);
  position: relative;
}

.blog-post__img img{
  width: 100%;
  height:100%;
  object-fit: cover;
  display: block;
  border-radius: .8rem;
}

.blog-post__img::before{
  content:"";
  width: 100%;
  height: 100%;
  position: absolute;
  top:0;
  left:0;
  box-shadow:  1rem 1rem 1rem rgba(23, 1, 63, 0.377);
  border-radius: .8rem;
}

.blog-post__date span{
  display: block;
  color: rgba(0,0,0,.5);
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0.5rem 0;
}

.blog-post__title{
  font-size: 2.5rem;
  margin-top: 1.5rem 0 2rem;
  text-transform: uppercase;
  color: #ffffff;
}

.blog-post__text{
  margin-bottom: 3rem;
  font-size: 1.4rem;
  color: rgba(255, 255, 255, 0.7);
}

.blog-post__cta{
  display: inline-block;
  padding: 1.5rem 3rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: white;
  background: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
  border-radius: .8rem;
  text-decoration: none;
}

.blog-post__cta:hover{
  background: linear-gradient(to right, #00f2fe 0%, #4facfe 100%);
}

