.eventCardWrapper{
  display: flex;
  width: 70%;
  justify-content: space-around;

}

.eventCard{
  width: 280px;
  height: 360px;
  padding: 2rem 1rem;
  background: #fff;
  position: relative;
  display: flex;
  align-items: flex-end;
  box-shadow: 0px 7px 10px rgba(0,0,0,0.5);
  transition: 0.5s ease-in-out;
  margin-bottom: 6rem;
}

.eventCard:hover{
  -webkit-transform: translateY(20px);
          transform: translateY(20px);

}

.eventCard:before{
  content: "";
  position: absolute;
  top:0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 82, 176, 0.5), rgb(170, 237, 240));
  z-index: 2;
  transition: 0.5s all;
  opacity: 0;
}

.eventCard:hover:before{
  opacity: 1;
}

.eventCard img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  position:absolute;
  top: 0;
  left: 0;
}

.eventCard .eventCardInfo{
  position: relative;
  z-index: 3;
  color: #fff;
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  transition: 0.5s all;
}

.eventCard:hover .eventCardInfo{
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);

}
.eventCard .eventCardInfo h1{
  margin: 0;
}

.eventCard .eventCardInfo p{
font-size: 15px;
letter-spacing: 1px;
margin-bottom: 20px;
margin-top: 8px;
}


/* FAQs */
.FAQ-section{
  font-family: "Quicksand", sans-serif;
  color: rgb(194, 228, 250);
  background-color: #040127;
  min-height: 100vh;
  width: 100vw;
  margin: 0 auto;
  flex-direction: column;
  display: flex;
  align-items: center;
  height: 100%;
  font-size: larger;
  margin-bottom: 0px;
}

.title{
  font-size: 3rem;
  margin: 2rem 0rem;
}



.faq{
  max-width: 70vw;
  margin-top: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid rgb(14, 1, 43);
  cursor: pointer;
}

.question{
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: ease-out 0.5s;
  height: 10vh;
  border-radius: 5px;
}
.question:hover{
background-color: rgba(255, 255, 255, 0.2);
}

.question h3{
  font-size:  1.8rem;
}

.answer{
  max-height: 0;
  overflow: hidden;
  transition: max-height 1.4s ease;
  color: white;
}

.answer p{
  padding-top: 0.5rem;
  line-height: 1.6;
  font-size: 1.4rem;
}

.faq.active .answer{
  max-height: 300px;
  -webkit-animation: fade 1s ease-in-out;
          animation: fade 1s ease-in-out;
}


.faq.active svg{
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
svg {
  transition: -webkit-transform .5s ease-in;
  transition: transform .5s ease-in;
  transition: transform .5s ease-in, -webkit-transform .5s ease-in;
}

@-webkit-keyframes fade {
  from {
      opacity: 0;
      -webkit-transform: translateY(-15vh);
              transform: translateY(-15vh);
  }
  to {
      opacity: 1;
      -webkit-transform: translateY(0vh);
              transform: translateY(0vh);
  }
}

@keyframes fade {
  from {
      opacity: 0;
      -webkit-transform: translateY(-15vh);
              transform: translateY(-15vh);
  }
  to {
      opacity: 1;
      -webkit-transform: translateY(0vh);
              transform: translateY(0vh);
  }
}

